.partners {
	padding: 16px 0;
	img {
		width: 160px;
		height: 67px;
	}

	&__container {
		align-items: center;
		gap: 6px;

		@include big-desktop {
			justify-content: space-between;
		}
		@include tablet {
			flex-wrap: wrap;
			justify-content: flex-start;
		}
	}

	&__logo {
		@include big-desktop {
			max-width: 130px;
		}
	}

	&__list {
		display: flex;
		align-items: center;
		gap: 12px;

		@include tablet {
			flex-wrap: wrap;
			justify-content: flex-start;
		}
	}

	&__title {
		margin: 0;
		color: var( --text-color);
		font-size: 11px;
		font-weight: 400;
		line-height: 145%;
		letter-spacing: 0.5px;
		text-transform: uppercase;
	}

	&__more {
		margin: 0;
		color: var( --text-color);
		font-size: 11px;
		font-weight: 400;
		line-height: 145%;
		letter-spacing: 0.5px;
		text-transform: uppercase;
		transition: color 0.3s ease;

		&:focus {
			outline: none;
		}

		@include hover {
			color: var(--accent-color);
		}

		&:active {
			color: var(--active-color);
		}
	}
}

.partners__more.focus-visible {
	 outline: 1px solid var(--active-color);
	 outline-offset: 3px;
}